import { clamp } from "../../core/utils";
import { enumColors } from "../colors";
import { RegularGameMode } from "./regular";

const colors = Object.values(enumColors);

export class PaintFrenzyGameMode extends RegularGameMode {
    constructor(root) {
        super(root);
    }
/*
    getUpgrades() {
        return upgrades;
    }

    getIsFreeplayAvailable() {
        return true;
    }

    getBlueprintShapeKey() {
        return blueprintShape;
    }

    getLevelDefinitions() {
        return levels;
    }*/

    getColorsForColorPatch(distanceToOriginInChunks) {
        return colors;
    }

    /**
     * Returns the size of a color patch.
     * @param {number} distanceToOriginInChunks 
     * @returns {number}
     */
    getColorPatchSize(distanceToOriginInChunks) {
        return Math.max(2, Math.round(1 + clamp(distanceToOriginInChunks / 8, 0, 4))) * 1.5;
    }
}