

import { GameRoot } from "./root";
import { clamp, findNiceIntegerValue, randomInt } from "../core/utils";
import { RandomNumberGenerator } from "../core/rng";
import { MapChunk } from "./map_chunk";
import { COLOR_ITEM_SINGLETONS } from "./items/color_item";
import { globalConfig } from "../core/config";
import { enumColors } from "./colors";
import { Vector } from "../core/vector";
import { enumSubShape } from "./shape_definition";

/** @typedef {{
 *   shape: string,
 *   amount: number
 * }} UpgradeRequirement */

/** @typedef {{
 *   required: Array<UpgradeRequirement>
 *   improvement?: number,
 *   excludePrevious?: boolean
 * }} TierRequirement */

/** @typedef {Array<TierRequirement>} UpgradeTiers */

/** @typedef {{
 *   shape: string,
 *   required: number,
 *   reward: enumHubGoalRewards,
 *   throughputOnly?: boolean
 * }} LevelDefinition */

export class GameMode {
    /**
     *
     * @param {GameRoot} root
     */
    constructor(root) {
        this.root = root;

        this.seed = randomInt(0, 100000);
    }

    /**
     * Should return all available upgrades
     * @returns {Object<string, UpgradeTiers>}
     */
    getUpgrades() {
        abstract;
        return null;
    }

    /**
     * Returns the blueprint shape key
     * @returns {string}
     */
    getBlueprintShapeKey() {
        abstract;
        return null;
    }

    /**
     * Returns the goals for all levels including their reward
     * @returns {Array<LevelDefinition>}
     */
    getLevelDefinitions() {
        abstract;
        return null;
    }

    /**
     * Should return whether free play is available or if the game stops
     * after the predefined levels
     * @returns {boolean}
     */
    getIsFreeplayAvailable() {
        return true;
    }

    /**
     * Should return how many bonus upgrade tiers you get by default.
     * Normally, this returns 0.
     * @returns {number}
     */
    getExtraUpgradeTier() {
        return 0;
    }

    /**
     * Should return the blueprint cost for the building count
     * @returns {number}
     */
    getBlueprintCost(buildingAmt) {
        return findNiceIntegerValue(4 * Math.pow(buildingAmt, 1.1));
    }

    /**
     * Should generate predefined resources for a given mapChunk and return true if something is generated.
     * @param {number} x 
     * @param {number} y 
     * @param {RandomNumberGenerator} rng 
     * @param {MapChunk} mapChunk 
     */
    generatePredefinedResources(x, y, rng, mapChunk) {
        if (x === 0 && y === 0) {
            mapChunk.internalGeneratePatch(rng, 2, COLOR_ITEM_SINGLETONS[enumColors.red], 7, 7);
            return true;
        }
        if (x === -1 && y === 0) {
            const item = this.root.shapeDefinitionMgr.getShapeItemFromShortKey("CuCuCuCu");
            mapChunk.internalGeneratePatch(rng, 2, item, globalConfig.mapChunkSize - 9, 7);
            return true;
        }
        if (x === 0 && y === -1) {
            const item = this.root.shapeDefinitionMgr.getShapeItemFromShortKey("RuRuRuRu");
            mapChunk.internalGeneratePatch(rng, 2, item, 5, globalConfig.mapChunkSize - 7);
            return true;
        }

        if (x === -1 && y === -1) {
            mapChunk.internalGeneratePatch(rng, 2, COLOR_ITEM_SINGLETONS[enumColors.green]);
            return true;
        }

        if (x === 5 && y === -2) {
            const item = this.root.shapeDefinitionMgr.getShapeItemFromShortKey("SuSuSuSu");
            mapChunk.internalGeneratePatch(rng, 2, item, 5, globalConfig.mapChunkSize - 7);
            return true;
        }

        if (this.getIsSmallWorldGen() && x === 6 && y === -4) {
            const item = this.root.shapeDefinitionMgr.getShapeItemFromShortKey("WuCuSuRu");
            mapChunk.internalGeneratePatch(rng, 3, item, 5, globalConfig.mapChunkSize - 7);
            return true;
        }

        return false;
    }

    /**
     * Returns a string[] containing the names of colors that can appear in a chunk.
     * @param {number} distanceToOriginInChunks
     * @returns {string[]}
     */
    getColorsForColorPatch(distanceToOriginInChunks) {
        let availableColors = [enumColors.red, enumColors.green];
            
        if (distanceToOriginInChunks > 2) {
            availableColors.push(enumColors.blue);
        }
        return availableColors;
    }

    /**
     * Returns the size of a color patch.
     * @param {number} distanceToOriginInChunks 
     * @returns {number}
     */
    getColorPatchSize(distanceToOriginInChunks) {
        return Math.max(2, Math.round(1 + clamp(distanceToOriginInChunks / 8, 0, 4)));
    }

    /**
     * Returns whether random color patches should be generated
     * @returns {boolean}
     */
    canGenerateRandomColorPatches() {
        return true;
    }

    /**
     * Which story rewards you gain from the start
     * @returns {Object.<string, number>}
    */
    getStartingRewards() {
        return {};
    }

    /**
     * Returns a vector indicating the size of the world (in chunks)
     * Default: infinite
     * @returns {Vector} Size
     */
    getMapChunkSize() {
        if (this.getIsSmallWorldGen()) {
            return new Vector(8,8);
        }
        return new Vector(Number.MAX_VALUE,Number.MAX_VALUE);
    }

    /**
     * Generates a random shape patch
     * @param {MapChunk} chunk 
     * @param {RandomNumberGenerator} rng 
     * @param {number} shapePatchSize 
     * @param {number} distanceToOriginInChunks 
     */
    generateRandomShapePatch(chunk, rng, shapePatchSize, distanceToOriginInChunks) {
        /** @type {[enumSubShape, enumSubShape, enumSubShape, enumSubShape]} */
        let subShapes = null;

        let weights = {};

        if (this.getIsSmallWorldGen()) {
            // Later there is a mix of everything
            weights = {
                [enumSubShape.rect]: 100,
                [enumSubShape.circle]: Math.round(50 + clamp(distanceToOriginInChunks * 4, 0, 50)),
                [enumSubShape.star]: Math.round(20 + clamp(distanceToOriginInChunks * 2, 0, 30)),
                [enumSubShape.windmill]: Math.round(6 + clamp(distanceToOriginInChunks * 3, 0, 20)),
            };

            if (distanceToOriginInChunks < 2/*7*/) {
                // Initial chunks can not spawn the good stuff
                weights[enumSubShape.star] = 0;
                weights[enumSubShape.windmill] = 0;
            }

            if (distanceToOriginInChunks < 4/*10*/) {
                // Initial chunk patches always have the same shape
                const subShape = chunk.internalGenerateRandomSubShape(rng, weights);
                subShapes = [subShape, subShape, subShape, subShape];
            } else if (distanceToOriginInChunks < 8/*15*/) {
                // Later patches can also have mixed ones
                const subShapeA = chunk.internalGenerateRandomSubShape(rng, weights);
                const subShapeB = chunk.internalGenerateRandomSubShape(rng, weights);
                subShapes = [subShapeA, subShapeA, subShapeB, subShapeB];
            } else {
                // Finally there is a mix of everything
                subShapes = [
                    chunk.internalGenerateRandomSubShape(rng, weights),
                    chunk.internalGenerateRandomSubShape(rng, weights),
                    chunk.internalGenerateRandomSubShape(rng, weights),
                    chunk.internalGenerateRandomSubShape(rng, weights),
                ];
            }
        } else {
            // Later there is a mix of everything
            weights = {
                [enumSubShape.rect]: 100,
                [enumSubShape.circle]: Math.round(50 + clamp(distanceToOriginInChunks * 2, 0, 50)),
                [enumSubShape.star]: Math.round(20 + clamp(distanceToOriginInChunks, 0, 30)),
                [enumSubShape.windmill]: Math.round(6 + clamp(distanceToOriginInChunks / 2, 0, 20)),
            };

            if (distanceToOriginInChunks < 7) {
                // Initial chunks can not spawn the good stuff
                weights[enumSubShape.star] = 0;
                weights[enumSubShape.windmill] = 0;
            }

            if (distanceToOriginInChunks < 10) {
                // Initial chunk patches always have the same shape
                const subShape = chunk.internalGenerateRandomSubShape(rng, weights);
                subShapes = [subShape, subShape, subShape, subShape];
            } else if (distanceToOriginInChunks < 15) {
                // Later patches can also have mixed ones
                const subShapeA = chunk.internalGenerateRandomSubShape(rng, weights);
                const subShapeB = chunk.internalGenerateRandomSubShape(rng, weights);
                subShapes = [subShapeA, subShapeA, subShapeB, subShapeB];
            } else {
                // Finally there is a mix of everything
                subShapes = [
                    chunk.internalGenerateRandomSubShape(rng, weights),
                    chunk.internalGenerateRandomSubShape(rng, weights),
                    chunk.internalGenerateRandomSubShape(rng, weights),
                    chunk.internalGenerateRandomSubShape(rng, weights),
                ];
            }
        }
        
        // Makes sure windmills never spawn as whole
        let windmillCount = 0;
        for (let i = 0; i < subShapes.length; ++i) {
            if (subShapes[i] === enumSubShape.windmill) {
                ++windmillCount;
            }
        }
        if (windmillCount > 1) {
            subShapes[0] = enumSubShape.rect;
            subShapes[1] = enumSubShape.rect;
        }

        const definition = this.root.shapeDefinitionMgr.getDefinitionFromSimpleShapes(subShapes);
        chunk.internalGeneratePatch(
            rng,
            shapePatchSize,
            this.root.shapeDefinitionMgr.getShapeItemFromDefinition(definition)
        );
    }

    /**
     * Returns the chance for a random shape patch to spawn in a chunk
     * @param {number} distanceToOriginInChunks 
     * @returns {number}
     */
    getShapePatchChance(distanceToOriginInChunks) {
        return 0.9 - clamp(distanceToOriginInChunks / 25, 0, 1) * 0.5;
    }

     /**
     * Returns the chance for a random color patch to spawn in a chunk
     * @param {number} distanceToOriginInChunks 
     * @returns {number}
     */
    getColorPatchChance(distanceToOriginInChunks) {
        return 0.9 - clamp(distanceToOriginInChunks / 25, 0, 1) * 0.5;
    }

    getIsSmallWorldGen() {
        return this.root.savegame.metaDataRef.extraOptions != undefined && this.root.savegame.metaDataRef.extraOptions["smallWorld"];
    }

    /**
     * @param {number} num
     * @param {any} shape1
     * @param {any} shape2
     * @param {any} shape3
     * @param {any} shape4
     * @param {number} baseAmt1
     * @param {number} baseAmt2
     * @param {number} baseAmt3
     * @param {number} baseAmt4
     * @param {number} increment1
     * @param {number} increment2
     * @param {number} increment3
     * @param {number} increment4
     */
    generateInfiniteUnlocks(num, shape1, shape2, shape3, shape4, baseAmt1, baseAmt2, baseAmt3, baseAmt4, increment1, increment2, increment3, increment4) {
        const rework = this.root.savegame.metaDataRef.extraOptions != undefined && this.root.savegame.metaDataRef.extraOptions["freeplayUpgradeRevamp"]
        return new Array(num).fill(null).map((_, i) => (calcRequired(this, i)));

        /**
         * @param {GameMode} mode
         * @param {number} index
         */
        function calcRequired(mode, index) {
            let shapes = [{ shape: shape1, amount: baseAmt1 + index * increment1 }];
            if (shape2 != undefined) {
                shapes.push({ shape: shape2, amount: baseAmt2 + index * increment2 });

                if (shape3 != undefined) {
                    shapes.push({ shape: shape3, amount: baseAmt3 + index * increment3 });

                    if (shape4 != undefined) {
                        shapes.push({ shape: shape4, amount: baseAmt4 + index * increment4 });
                    }
                }
            }

            //if (shapes.length < 4 && rework) {
            //    shapes.push({shape: computeFreeplayShape(mode.root, mode.seed, index), amount: 5000})
            //}

            const upgrade = {
                required: shapes,
                excludePrevious: true,
            }

            return upgrade;
        }
    }
}
