import { findNiceIntegerValue } from "../../core/utils";
import { GameMode } from "../game_mode";
import { ShapeDefinition } from "../shape_definition";
import { enumHubGoalRewards } from "../tutorial_goals";

const rocketShape = "CbCuCbCu:Sr------:--CrSrCr:CwCwCwCw";
const finalGameShape = "RuCw--Cw:----Ru--";
const preparementShape = "CpRpCp--:SwSwSwSw";
const blueprintShape = "CBCBCBRB:CwCwCwCw";

// Tiers need % of the previous tier as requirement too
const tierGrowth = 2.5;

/**
 * Generates all upgrades
 * @returns {Object<string, import("../game_mode").UpgradeTiers>} */
function generateUpgrades(limitedVersion = false) {
    const fixedImprovements = [0.5, 0.5, 1, 1, 2, 1, 1];
    const numEndgameUpgrades = limitedVersion ? 0 : 1000 - fixedImprovements.length - 1;

    // Fill in endgame upgrades
    for (let i = 0; i < numEndgameUpgrades; ++i) {
        if (i < 20) {
            fixedImprovements.push(0.1);
        } else if (i < 50) {
            fixedImprovements.push(0.05);
        } else if (i < 100) {
            fixedImprovements.push(0.025);
        } else {
            fixedImprovements.push(0.0125);
        }
    }

    const upgrades = {
        belt: [
            {
                required: [{ shape: "CBCBCBCB", amount: 2500 }],
            },
        ],

        miner: [
            {
                required: [{ shape: "CB--CBRB:CwSwCwRw", amount: 2500 }],
            },
        ],

        processors: [
            {
                required: [{ shape: "SB----SB:CwCwCwCw:WBCyCyWB", amount: 2500 }],
            },
        ],

        painting: [
            {
                required: [{ shape: "RbRb----", amount: 2500 }],
            },
        ],
    };

    // Automatically generate tier levels
    for (const upgradeId in upgrades) {
        const upgradeTiers = upgrades[upgradeId];

        let currentTierRequirements = [];
        for (let i = 0; i < upgradeTiers.length; ++i) {
            const tierHandle = upgradeTiers[i];
            tierHandle.improvement = fixedImprovements[i];
            const originalRequired = tierHandle.required.slice();

            for (let k = currentTierRequirements.length - 1; k >= 0; --k) {
                const oldTierRequirement = currentTierRequirements[k];
                if (!tierHandle.excludePrevious) {
                    tierHandle.required.unshift({
                        shape: oldTierRequirement.shape,
                        amount: oldTierRequirement.amount,
                    });
                }
            }
            currentTierRequirements.push(
                ...originalRequired.map(req => ({
                    amount: req.amount,
                    shape: req.shape,
                }))
            );
            currentTierRequirements.forEach(tier => {
                tier.amount = findNiceIntegerValue(tier.amount * tierGrowth);
            });
        }
    }

    // VALIDATE
    if (G_IS_DEV) {
        for (const upgradeId in upgrades) {
            upgrades[upgradeId].forEach(tier => {
                tier.required.forEach(({ shape }) => {
                    try {
                        ShapeDefinition.fromShortKey(shape);
                    } catch (ex) {
                        throw new Error("Invalid upgrade goal: '" + ex + "' for shape" + shape);
                    }
                });
            });
        }
    }

    return upgrades;
}

/**
 * Generates the level definitions
 */
export function generateLevelDefinitions() {
    const levelDefinitions = [
        // 1 TOTEMS
        {
            shape: "CuCuCuCu",
            required: 5,
            reward: enumHubGoalRewards.reward_totem,
        },
        // 2 Black circle
        {
            shape: "CBCBCBCB", // belts t1
            required: 1500,
            reward: enumHubGoalRewards.no_reward,
        },
        // 3 BLUEPRINTS
        {
            shape: blueprintShape,
            required: 2500,
            reward: enumHubGoalRewards.reward_blueprints,
        },
        // 4 UNLOCKS Ceremonies
        {
            shape: "SB----SB:CwCwCwCw:WBCyCyWB",
            required: 5000,
            reward: enumHubGoalRewards.reward_ritual_totem,
        },         
    ];

    if (G_IS_DEV) {
        levelDefinitions.forEach(({ shape }) => {
            try {
                ShapeDefinition.fromShortKey(shape);
            } catch (ex) {
                throw new Error("Invalid tutorial goal: '" + ex + "' for shape" + shape);
            }
        });
    }

    return levelDefinitions;
}

const upgrades = generateUpgrades(false);

const levels = generateLevelDefinitions();

export class BlackPaintGameMode extends GameMode {
    constructor(root) {
        super(root);
    }

    getUpgrades() {
        return upgrades;
    }

    getIsFreeplayAvailable() {
        return true;
    }

    getBlueprintShapeKey() {
        return blueprintShape;
    }

    getLevelDefinitions() {
        return levels;
    }
    
    getExtraUpgradeTier() {
        return 5;
    }

    getStartingRewards() {
        return {
            [enumHubGoalRewards.no_reward]: 1,
            [enumHubGoalRewards.reward_balancer]: 1,
            [enumHubGoalRewards.reward_logic_gates]: 1,
            [enumHubGoalRewards.reward_merger]: 1,
            [enumHubGoalRewards.reward_miner_chainable]: 1,
            [enumHubGoalRewards.reward_mixer]: 1,
            [enumHubGoalRewards.reward_painter]: 1,
            [enumHubGoalRewards.reward_painter_double]: 1,
            [enumHubGoalRewards.reward_rotater]: 1,
            [enumHubGoalRewards.reward_rotater_180]: 1,
            [enumHubGoalRewards.reward_rotater_ccw]: 1,
            [enumHubGoalRewards.reward_splitter]: 1,
            [enumHubGoalRewards.reward_stacker]: 1,
            [enumHubGoalRewards.reward_storage]: 1,
            [enumHubGoalRewards.reward_tunnel]: 1,
            [enumHubGoalRewards.reward_underground_belt_tier_2]: 1,
            [enumHubGoalRewards.reward_virtual_processing]: 1,
            [enumHubGoalRewards.reward_wires_painter_and_levers]: 1,
            [enumHubGoalRewards.reward_cutter_and_trash]: 1,
            [enumHubGoalRewards.reward_cutter_quad]: 1,
            [enumHubGoalRewards.reward_belt_reader]: 1,
            [enumHubGoalRewards.reward_constant_signal]: 1,
            [enumHubGoalRewards.reward_display] : 1,
            [enumHubGoalRewards.reward_filter]: 1,
         };
    }
}