import { generateMatrixRotations } from "../../core/utils";
import { enumDirection, Vector } from "../../core/vector";
import { ItemAcceptorComponent } from "../components/item_acceptor";
import { enumItemProcessorTypes, ItemProcessorComponent } from "../components/item_processor";
import { Entity } from "../entity";
import { defaultBuildingVariant, MetaBuilding } from "../meta_building";
import { GameRoot } from "../root";
import { enumHubGoalRewards } from "../tutorial_goals";
import { ItemEjectorComponent } from "../components/item_ejector";

const overlayMatrix = generateMatrixRotations([1, 1, 0, 1, 1, 1, 0, 1, 1]);

/** @enum {string} */
export const enumTotemVariants = { ritual: "ritual" };

export class MetaTotemBuilding extends MetaBuilding {
    constructor() {
        super("totem");
    }

    getIsRotateable() {
        return true;
    }

    getSilhouetteColor() {
        return "#9b9991";
    }

    getDimensions(variant) {
        switch (variant) {
            case defaultBuildingVariant:
                return new Vector(1,1);
            case enumTotemVariants.ritual:
                return new Vector(2,2);
            default:
                assertAlways(false, "Unknown painter variant: " + variant);
        }
    }

    getSpecialOverlayRenderMatrix(rotation) {
        return overlayMatrix[rotation];
    }

    /**
     * @param {GameRoot} root
     */
    getAvailableVariants(root) {
        let variants = [defaultBuildingVariant];
        if (root.hubGoals.isRewardUnlocked(enumHubGoalRewards.reward_ritual_totem)) {
            variants.push(enumTotemVariants.ritual);
        }
        return variants;
    }

    /**
     * @param {GameRoot} root
     */
    getIsUnlocked(root) {
        return root.hubGoals.isRewardUnlocked(enumHubGoalRewards.reward_totem);
    }

    /**
     * Creates the entity at the given location
     * @param {Entity} entity
     */
    setupEntityComponents(entity) {
        entity.addComponent(
            new ItemAcceptorComponent({
                slots: [
                    {
                        pos: new Vector(0, 0),
                        directions: [
                            enumDirection.bottom,
                        ],
                        filter: "shape",
                    },
                ],
            })
        );
        
        entity.addComponent(
            new ItemProcessorComponent({
                inputsPerCharge: 1,
                processorType: enumItemProcessorTypes.totem,
            })
        );
        entity.addComponent(
            new ItemEjectorComponent({
                slots: [{ pos: new Vector(0, 0), direction: enumDirection.top }],
            })
        );
    }

    /**
     *
     * @param {Entity} entity
     * @param {number} rotationVariant
     * @param {string} variant
     */
    updateVariants(entity, rotationVariant, variant) {
        switch (variant) {
            case defaultBuildingVariant: {
                break;
            }
            case enumTotemVariants.ritual: {
                entity.components.ItemAcceptor.setSlots([
                    {
                        pos: new Vector(0, 1),
                        directions: [
                            enumDirection.bottom,
                        ],
                        filter: "shape",
                    },
                    {
                        pos: new Vector(1, 1),
                        directions: [
                            enumDirection.bottom,
                        ],
                        filter: "color",
                    },
                ]);
            break;
            }
                    
        }
    }
}
